<template>
  <div>
    <!-- <el-input
      placeholder="请输入内容"
      prefix-icon="el-icon-search"
      clearable
      style="width: 500px"
      maxlength="10"
      show-word-limit
      v-model="input"
    >
    </el-input> -->

    <!-- 表格 -->
    <el-button @click="addStuDialogVisible = true" style="float:right ;margin-top:-10px;margin-bottom:10px">添加学生</el-button>
    <!-- 添加学生的dialog -->
    <el-dialog title="添加学生" :visible.sync="addStuDialogVisible">
      <el-form :model="addStuForm">
        <span>当前页仅添加基本信息,其余项点击进行编辑修改</span>
        <el-form-item label="姓名" prop="stu_Name" label-width="150px">
          <el-input v-model="addStuForm.stu_Name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item prop="title" label="基本信息" label-width="150px">
          <el-input v-model="addStuForm.title"></el-input>
        </el-form-item>
        <el-form-item prop="stu_Address" label="地址" label-width="150px">
          <el-input v-model="addStuForm.stu_Address"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addStuDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addStu">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 表格数据 -->
    <el-table ref="filterTable" :data="userList" style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="scope">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="学号">
              <span>{{ scope.row.stu_Id }}</span>
            </el-form-item>
            <el-form-item label="学生姓名">
              <span>{{ scope.row.stu_Name }}</span>
            </el-form-item>
            <el-form-item label="专业班级">
              <span
                >{{ scope.row.profession }} {{ scope.row.stu_Grade }}0{{
                  scope.row.stu_Class
                }}</span
              >
            </el-form-item>
            <el-form-item label="辅导员">
              <span>{{ scope.row.counsellor }}</span>
            </el-form-item>
            <el-form-item label="入学时间" width="180"
              ><span>{{ scope.row.date }}</span></el-form-item
            >
            <el-form-item label="家庭地址">
              <span>{{ scope.row.stu_Address }}</span>
            </el-form-item>
            <el-form-item label="信息说明" width="180"
              ><span>{{ scope.row.title }}</span></el-form-item
            >
          </el-form>
        </template>
      </el-table-column>
      <el-table-column prop="Id" label="ID" width="150"> </el-table-column>
        <el-table-column
        prop="college"
        sortable
        label="学院"
        width="180"
        :filters="selectCollege"
        :filter-method="filterHandler"
        filter-placement="bottom-end"
      >
      <!-- <el-tag>{{selectCourse[0].text}}</el-tag> -->

        <template slot-scope="scope">
          <el-tag v-if="scope.row.college === selectCollege[0].text">{{
            scope.row.college
          }}</el-tag>
          <el-tag
            type="success"
            v-else-if="scope.row.college === selectCollege[1].text"
            >{{ scope.row.college }}</el-tag
          >
          <el-tag
            type="warning"
            v-else-if="scope.row.college === selectCollege[2].text"
            >{{ scope.row.college }}</el-tag
          >
          <el-tag type="danger" v-else>{{ scope.row.college }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="stu_Id" label="学号" width="200">
      </el-table-column>
      <el-table-column prop="stu_Name" label="学生姓名" width="180">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <!-- scope.$index 获取当前行索引 -->
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
            >编辑</el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row.stu_Id)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <!-- 修改学生信息 -->
    <el-dialog title="修改信息" :visible.sync="EditStuDialogVisible">
      <el-form :model="editStuForm">
        <el-form-item label="姓名" prop="stu_Name" label-width="150px">
          <el-input
            v-model="editStuForm.stu_Name"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item prop="title" label="基本信息" label-width="150px">
          <el-input v-model="editStuForm.title"></el-input>
        </el-form-item>
        <el-form-item prop="stu_Address" label="地址" label-width="150px">
          <el-input v-model="editStuForm.stu_Address"></el-input>
        </el-form-item>
        <el-form-item prop="date" label="入学日期" label-width="150px">
          <el-input v-model="editStuForm.date" disabled></el-input>
        </el-form-item>
        <el-form-item prop="counsellor" label="辅导员" label-width="150px">
          <el-select v-model="editStuForm.counsellor" placeholder="请选择">
            <el-option
              v-for="item in selectCounsellor"
              :key="item.value"
              :label="item.counsellor"
              :value="item.counsellor">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="college" label="专业学院" label-width="150px">
          <template class="selectTeacher">
            <el-select v-model="editStuForm.college" placeholder="请选择">
              <el-option
                v-for="item in selectCollege"
                :key="item.value"
                :label="item.text"
                :value="item.text">
              </el-option>
            </el-select>
          </template>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="EditStuDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="EditStu">确 定</el-button>
      </div>
    </el-dialog>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageInfo.pageIndex"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageInfo.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isEdit: false,
      input: '',
      userList: [],
      total: 0,
      pageInfo: {
        pageIndex: 1,
        pageSize: 10
      },
      addStuForm: {
        stu_Name: '',
        title: '',
        stu_Address: '',
        stu_Id: ''
      },
      editStuForm: {
        stu_Id: '',
        stu_Name: '',
        title: '',
        stu_Address: '',
        date: '',
        stu_Class: '',
        stu_Grade: '',
        profession: '',
        college: '',
        counsellor: ''
      },
      addStuDialogVisible: false,
      EditStuDialogVisible: false,
      selectCounsellor: [],
      selectCollege: []
    }
  },
  methods: {
    filterHandler (value, row, column) {
      const property = column.property
      return row[property] === value
    },

    // 获取全部数据
    getAllList () {
      this.axios.get('/api/getAll/stuInfo').then((res) => {
        const array1 = res.data.list
        const m1 = new Map()
        const unique1 = (array, key) => {
          for (const item of array) {
            if (!m1.has(item[key])) {
              m1.set(item[key], item)
            }
          }
          return [...m1.values()]
        }
        const a1 = unique1(array1, 'counsellor')
        for (let i = 0; i < a1.length; i++) {
          this.selectCounsellor.push({ value: a1[i].counsellor, counsellor: a1[i].counsellor })
        }

        const m2 = new Map()
        const unique2 = (array, key) => {
          for (const item of array) {
            if (!m2.has(item[key])) {
              m2.set(item[key], item)
            }
          }
          return [...m2.values()]
        }
        const a2 = unique2(array1, 'college')
        for (let i = 0; i < a2.length; i++) {
          this.selectCollege.push({ value: a2[i].college, text: a2[i].college })
        }
      })
    },
    // 分页获取数据
    getUserList () {
      this.axios
        .get('/api/getByPage/stuInfo', {
          params: this.pageInfo
        })
        .then((res) => {
          this.userList = res.data.list
          this.total = res.data.total
        })
    },
    addStu () {
      this.axios
        .post('/api/add/stuInfo', {
          stu_Name: this.addStuForm.stu_Name,
          title: this.addStuForm.title,
          stu_Address: this.addStuForm.stu_Address,
          stu_Id: this.total + 1
        })
        .then((res) => {
          this.addStuDialogVisible = false
          this.getUserList()
        })
    },
    handleEdit (id, row) {
      this.EditStuDialogVisible = true
      this.editStuForm = row
    },
    EditStu (id) {
      this.axios
        .post('/api/edit/stuInfo', {
          Id: this.editStuForm.Id,
          stu_Name: this.editStuForm.stu_Name,
          title: this.editStuForm.title,
          stu_Address: this.editStuForm.stu_Address,
          stu_Id: this.editStuForm.stu_Id,
          stu_Class: this.editStuForm.stu_Class,
          stu_Grade: this.editStuForm.stu_Grade,
          date: this.editStuForm.date,
          counsellor: this.editStuForm.counsellor,
          profession: this.editStuForm.profession,
          college: this.editStuForm.college
        })
        .then((res) => {
          this.EditStuDialogVisible = false
          this.getUserList()
        })
    },
    handleDelete (id) {
      this.axios
        .post('/api/del/stuInfo', {
          id
        })
        .then((res) => {
          if ((res.data.total / 10) % 1 === 0 && this.pageInfo.pageIndex === ((res.data.total / 10) + 1)) {
            this.pageInfo.pageIndex = this.pageInfo.pageIndex - 1
            this.getUserList()
          }
          this.getUserList()
        })
    },
    // 分页 handleSizeChange() handleCurrentChange()
    // 监听 pageSizeChange
    handleSizeChange (newSize) {
      this.pageInfo.pageSize = newSize
      this.getUserList()
    },
    // 监听 pagenum改变
    handleCurrentChange (newPage) {
      this.pageInfo.pageIndex = newPage
      this.getUserList()
    }
  },
  created () {
    this.getAllList()
    this.getUserList()
  }
}
</script>

<style lang="less">
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  margin-left: 100px;
  width: 35%;
}

</style>
